@import "utils/variables.scss";
@import "utils/mixins.scss";

.compare {
  &__bike {
    margin-bottom: 40px;
  }

  &__title {
    margin-bottom: 32px;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    white-space: nowrap;
    text-align: center;

    @include onIpad {
      font-size: $title-size-ipad;
    }

    @include onDesktop {
      font-size: $title-size-desktop;
    }
  }
}
